
.keystone-bg {
    /* The image used */
    background-image: url("../media/steamboat-ski-resort-night-skiing-colorado-1280x640.jpg");
  
    /* Full height */
    height: 100vh;
  
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size:  cover;
  }

  .park-city-bg {
    /* The image used */
    background-image: url("../media/krzysztof-kowalik-t8p9dbMpAzg-unsplash.jpg");
  
    /* Full height */
    height: 100vh;
  
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size:  cover;
  }

  @media screen and (max-width: 768px) {
    .park-city-bg {
      height: 50vh;
    }
    .keystone-bg {
      height: 50vh;
    }
  }